import smoothscroll from 'smoothscroll-polyfill';

// kick off the polyfill!
smoothscroll.polyfill();

// Scroll to a certain element
let elements = document.querySelectorAll('.scroll');
let navbarOffset = 62;

elements.forEach(function (element) {
    element.addEventListener('click', function(e) {
        e.preventDefault();
        if(element.classList.contains('totop')) {
            scrollToPx(0);
        } else {
            let id = element.getAttribute("href");
            let item = document.querySelector(id);
            let main = document.getElementsByTagName('body')[0];
            let position = item.offsetTop - main.scrollTop - navbarOffset;
            
            scrollToPx(position);
        }
    });
});

if(window.location.hash) {
    window.onload = function() {
        let id = window.location.hash;
        let item = document.querySelector(id);
        let main = document.getElementsByTagName('body')[0];
        let position = item.offsetTop - main.scrollTop - navbarOffset;

        scrollToPx(position);
    }
}

// function to scroll to position based on px value
function scrollToPx (px) {
    window.scrollTo({
        top: px,
        behavior: 'smooth'
    })
}