if (document.getElementById('header')) {

    // Offscreen Navigation
    const navTriggers = document.querySelectorAll(".bt-menu-trigger")

    var navOverlay = document.getElementById('body-overlay');
    var hamburgers = document.getElementsByClassName('nav-icon');
    var panel = document.getElementById('offscreen-nav');

    
    function closeNav() {
        panel.classList.remove('cd-panel--is-visible');
        // hamburger.classList.remove("bt-menu-open");
        document.querySelector('body').style.overflow = 'visible';
        navOverlay.style.opacity = '0';
        navOverlay.style.visibility = 'hidden';
        for (var i = 0; i < hamburgers.length; i++) {
            hamburgers[i].classList.remove("open");
        }
    }

    function openNav() {
        panel.classList.add('cd-panel--is-visible');
        // hamburger.classList.add("bt-menu-open");
        document.querySelector('body').style.overflow = 'hidden';
        navOverlay.style.opacity = '.95';
        navOverlay.style.visibility = 'visible';

        for (var i = 0; i < hamburgers.length; i++) {
            hamburgers[i].classList.add("open");
        }
    }

    // Open / Close Navigation
    for (const navTrigger of Array.from(navTriggers)) {
        navTrigger.addEventListener('click', function (event) {
            if (panel.classList.contains('cd-panel--is-visible')) {
                closeNav();
            } else {
                openNav();
            }
        })
    }

    // Close Navigation when pressing esc
    document.onkeydown = function (evt) {
        evt = evt || window.event;
        if (evt.keyCode == 27) {
            if (panel.classList.contains('cd-panel--is-visible')) {
                closeNav();
            }
        }
    };

    // navbar animation
    var scrollpos = window.scrollY;
    var header = document.getElementById("header");
    var navcontent = document.getElementById("nav-content");
    var btMenu = document.querySelector('#hamburger .bt-menu');
    var brand = header.querySelector('.brand');
    var logo = header.querySelector('.logo');
    var langNav = header.querySelector('#langNavDropdown');

    document.addEventListener('scroll', function () {

        /*Apply classes for slide in bar*/
        scrollpos = window.scrollY;

        if (scrollpos > 100) {
             header.classList.add("bright");
            header.classList.add("bg-white");
            header.classList.add("md:py-0");
            header.classList.remove("md:py-16");
            header.classList.add("shadow");

            // resize brand logo on scroll
            if(logo) {
                var small = logo.dataset.scrollwidth;
                if (logo.getElementsByTagName('svg')[0]) {
                    logo.getElementsByTagName('svg')[0].style.width = small;
                }
                if (logo.getElementsByTagName('img')[0]) {
                    logo.getElementsByTagName('img')[0].style.width = small;
                }
            }
            if(btMenu) {
                btMenu.classList.remove('bt-menu-big');
                btMenu.querySelector('.bt-menu-trigger').classList.add('bt-menu-active');
            }

            if (langNav && langNav.dataset.bg == 'white') {
                langNav.classList.remove('bg-black');
                langNav.classList.add('bg-white');
            }

        } else {
            header.classList.remove("bright");
            header.classList.remove("bg-white");
            header.classList.remove("md:py-0");
            header.classList.add("md:py-16");
            header.classList.remove("shadow");

            // resize brand logo on scroll
            if (logo) {
                var big = logo.dataset.startwidth;
                if (logo.getElementsByTagName('svg')[0]) {
                    logo.getElementsByTagName('svg')[0].style.width = big;
                }
                if (logo.getElementsByTagName('img')[0]) {
                    logo.getElementsByTagName('img')[0].style.width = big;
                }
            }
            if(btMenu) {
                btMenu.classList.add('bt-menu-big');
                btMenu.querySelector('.bt-menu-trigger').classList.remove('bt-menu-active');
            }

            if (langNav && langNav.dataset.bg == 'white') {
                langNav.classList.add('bg-black');
                langNav.classList.remove('bg-white');
            }

        }

    });

}